<template>
  <div class="VimeImage">
       <div class="image">
            <el-carousel  class="el-carousel" :autoplay="false" indicator-position="none" :initial-index="id">
                <el-carousel-item class="el-carousel-item" v-for="(car,index) in banner" :key="index">
                    <img :src="car">
                </el-carousel-item>
            </el-carousel>
        </div>
  </div>
</template>

<script>
export default {
  name: 'VimeImage',
  data () {
    return {
        banner:[],
        id: this.$route.query.id,
    }
  },
  methods:{
      requestImg(){
          for(var i = 0;i<50;i++){
             this.banner.push(require("../assets/move/"+(i+1)+"-m.jpg"))
          }
      }

  },
  mounted(){
    window.scrollTo(0,200);
    this.requestImg();
  }
}
</script>

<style lang="less" scoped>
.VimeImage{
    width: 100%;
    height: 100%;
    background: #000000;
}
.image{
      width: 1170px;
      margin: 0 auto;
      background: #000000;
      margin-bottom: 20px;
  }
  .el-carousel__item{
      img{
          width: 100%;
          height: 100%;
          cursor: pointer;
      }
  }
</style>



